import React from 'react';

function NavBar() {
    return (
        <header className="w-full bg-white text-gray-600 body-font shadow z-40 md:fixed">
            <div className="container mx-auto flex flex-wrap p-5 flex-col md:flex-row items-center">
                <a className="flex title-font font-medium items-center text-gray-900 mb-4 md:mb-0">
                    <img className="img-responsive h-16"
                         src='img/logo.png'/>
                    <span className="ml-3 text-xl">Robot House Electronic</span>
                </a>
                <nav className="md:ml-auto flex flex-wrap items-center text-base justify-center">
                    <a href="#home"
                        className="mr-2 rounded-lg transition cursor-pointer p-4 hover:text-white hover:bg-gray-500">Home</a>
                    <a href="#kelas"
                        className="mr-2 rounded-lg transition cursor-pointer p-4 hover:text-white hover:bg-gray-500">Course</a>
                    <a href="#why?" 
                        className="mr-2 rounded-lg transition cursor-pointer p-4 hover:text-white hover:bg-gray-500">Why?</a>
                    <a href="#harga"
                        className="mr-2 rounded-lg transition cursor-pointer p-4 hover:text-white hover:bg-gray-500">Price</a>
                    <a href="#kontak"
                        className="mr-2 rounded-lg transition cursor-pointer p-4 hover:text-white hover:bg-gray-500">Contact</a>
                </nav>
            </div>
        </header>
    );
}

export default NavBar;