import React from 'react';

function TentangMereka() {
    return ( 
    <section className = "text-gray-600 body-font">
        <div className = "container px-5 pb-24 mx-auto" >
            <div className = "flex flex-col text-center w-full mb-10" >
                <h1 className = "text-2xl font-medium title-font mb-4 text-gray-900 tracking-widest" > 
                Masih Ragu ? 
                </h1> 
                    <p className = "lg:w-2/3 mx-auto leading-relaxed text-base" >
                        Saksikan apa kata orang hebat bahwa pentingnya belajar memprogram sejak dini. 
                    </p> 
            </div>

            <div className = "relative h-0 pb-fluid-video" >
                <iframe className = "absolute top-0 left-0 w-full h-full"
                    src = "https://www.youtube.com/embed/nKIu9yen5nc"
                    allow = "accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen>
                </iframe> 
            </div> 
        </div> 
    </section>
    );
}

export default TentangMereka;